	module.exports = {"data":{"markdownRemark":{"html":"<h2>Conditional and Biconditional Statements</h2>\n<p><strong>Conditional statement:</strong> “if”. If one thing is true, then something else is true or can happen. <em>Example:</em> if it rains right now, then the ground will be wet. <em>However</em>, the converse (“if the ground is wet, then it is raining”) is not necessarily true.</p>\n<p><strong><em>Bi</em></strong><strong>conditional statement:</strong> “if and only if”. If one thing is true, then something else is true; and if that something else is true, then that the one thing is true. Another way to think about it is that two things are either both true, or both false (not one true and the other false). <em>Example:</em> A square has an area of 16 cubic meters if and only if its sides are both 4 meters long.</p>","fields":{"slug":"/logic/conditional-and-biconditional-statements/"},"frontmatter":{"title":"Conditional and Biconditional Statements"}}},"pathContext":{"slug":"/logic/conditional-and-biconditional-statements/"}}

